@import "style/mixins.module.scss";

.root {
    @include respond-to("laptop") {
        display: flex;
        justify-content: space-between;
    }
}

.title {
    margin-bottom: 8px;
    text-align: center;

    @include respond-to("laptop") {
        text-align: left;
        margin-bottom: 16px;
    }
}

.address {
    font-size: 17px;
    line-height: 24px;
    text-align: center;
    font-weight: 400;
    margin-bottom: 24px;

    @include respond-to("laptop") {
        text-align: left;
        margin-bottom: 32px;
        max-width: 400px;
        font-size: 22px;
        line-height: 32px;
    }
}

.button-container {
    text-align: center;

    @include respond-to("laptop") {
        text-align: left;
    }
}

.link-container {
    text-align: center;

    @include respond-to("laptop") {
        width: 50%;
    }
}

.button {
    margin-bottom: 24px;

    @include respond-to("laptop") {
        margin-bottom: 0;
    }
}

.link-map {
    color: #45a1f8;
    text-transform: capitalize;
    text-align: center;
    font-weight: 700;
    font-size: 22px;
    line-height: 32px;
    display: block;
}

.image {
    display: block;
    height: 240px;
    width: 100%;
    border-radius: 8px;
    object-fit: cover;

    @include respond-to("laptop") {
        height: 400px;
    }
}

.left-column {
    @include respond-to("laptop") {
        width: calc(100% / 12 * 5);
    }
}
