@import "style/mixins.module.scss";

.root {
    display: flex;
    flex-direction: column;
}

.container {
    width: 100%;
}

.header {
    width: 100%;
    padding: 0 24px;

    @include respond-to("laptop") {
        padding: 0 40px;
    }

    @include respond-to("laptop-L") {
        max-width: 1200px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 10px;
        margin-bottom: 10px;

        padding: 0;
    }

    @include respond-to("laptop-XL") {
        max-width: 1600px;
        padding: 0 40px;
    }
}

.main {
    padding-bottom: 64px;

    @include respond-to("laptop") {
        padding-bottom: 120px;
    }
}

.steps {
    padding-bottom: 64px;

    @include respond-to("laptop") {
        padding-bottom: 120px;
    }
}

.clinic-visit,
.prices,
.map,
.appointment {
    padding-bottom: 64px;

    @include respond-to("laptop") {
        padding-bottom: 120px;
    }
}

.advantages {
    margin-bottom: 40px;

    @include respond-to("laptop") {
        margin-bottom: 120px;
    }
}

.prices {
    @include respond-to("mobile") {
        margin-bottom: 40px;
    }
}

.questions {
    margin-bottom: 80px;

    @include respond-to("laptop") {
        margin-bottom: 224px;
    }
}

.prices {
    @include respond-to("mobile") {
        margin-bottom: 40px;
    }
}

.appointment-title {
    @include formTitle();
}